.header {
  background-color: $secondary-bg-color;
  color: $white-color;

  &__content {
    @include onDesktop {
      position: relative;
      height: 100vh;
      max-width: 1280px;
      margin: 0 auto;
    }
  }

  &__top {
    position: relative;
    margin-bottom: 50px;
    z-index: 1;

    @include onTablet {
      margin-bottom: 99px;
    }

    @include onDesktop {
      margin-bottom: 90px;
    }
  }

  &__bottom {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__data {
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 10px;

    @include onTablet {
      grid-column: 1 / 3;
    }
  }

  &__title {
    font-family: 'Playfair Display', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 100%;
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 20px;
      grid-column: 1 / 6;
      font-size: 72px;
    }

    @include onDesktop {
      grid-column: 1 / 7;
      font-size: 96px;
      margin-bottom: 30px;
    }
  }

  &__subtitle {
    max-width: 330px;
    margin-bottom: 40px;

    @include onTablet {
      max-width: 100%;
      margin-bottom: 50px;
      grid-column: 1 / 4;
    }

    @include onDesktop {
      margin-bottom: 81px;
      grid-column: 1 / 7;
    }
  }

  &__decoration {
    display: none;

    @include onDesktop {
      display: block;
      position: absolute;
      bottom: 50px;
      left: 0;
      font-family: "Playfair Display", sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: 125%;
      opacity: 0.7;

      &::after {
        position: absolute;
        top: 60%;
        left: 101px;
        content: '';
        width: 469px;
        height: 1px;
        background-color: $white-color;
      }
    }
  }

  &__img-wrapper {
    padding: 0 18px;
    max-width: 443px;
    margin: 0 auto;

    @include onTablet {
      max-width: 100%;
      margin: 0;
      padding: 0;
      grid-column: 2 / 6;
    }

    @include onDesktop {
      position: absolute;
      right: -55px;
      bottom: 0;
      width: 625px;
    }
  }

  &__img {
    box-sizing: border-box;
    display: block;
    width: 100%;
  }

  &__button {
    max-width: 330px;
    margin-bottom: 40px;

    @include onTablet {
      max-width: 100%;
      grid-column: 1 / 4;
      margin-bottom: 70px;
    }
  }
}