.button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  background-color: $orange-color;
  color: $white-color;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  transition: all $effectDuration;

  &:hover {
    background-color: #AF3419;
  }

  &:active {
    background-color: #EA340D;
  }

  &--arrow {
    background-color: transparent;
    color: $orange-color;
    border: 1px solid $orange-color;
    gap: 8px;
    transition: all $effectDuration;

    &:hover {
      color: #AF3419;
      border-color: #AF3419;
      background-color: transparent;

    }

    &:hover .icon--arrow {
      transition: stroke $effectDuration;
      animation: arrow-movement 0.3s ease-in;
      stroke: #AF3419;
    }

    &:active {
      color: #EA340D;
      border-color: #EA340D;
      background-color: transparent;
    }

    &:active .icon--arrow {
      transition: stroke $effectDuration;
      stroke: #EA340D;
    }


    &--mobile {
      @include onTablet {
        display: none;
      }
    }
  }
}