.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
  transition: all $effectDuration;

  &--menu {
    background-image: url("../images/icons/menu.svg");
    width: 25px;
    height: 20px;
    @include hover(transform, scale(1.15));

    @include onTablet {
      width: 30px;
      height: 30px;
    }
  }

  &--close {
    background-image: url("../images/icons/close.svg");
    width: 22px;
    height: 22px;
    @include hover(transform, scale(1.15));

    @include onDesktop {
      width: 30px;
      height: 30px;
    }
  }

  &--arrow {
    stroke: $orange-color;
    transition: stroke $effectDuration;
  }

  &--footer {
    fill: $white-color;
    width: 20px;
    height: 20px;

    &:hover {
      transition: fill $effectDuration;
      fill: $orange-color;
    }
  }
}