.page {
  background-color: $main-bg-color;
  font-family: 'Raleway', sans-serif;
  color: $main-text-color;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  scroll-behavior: smooth;

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    transform: translateY(-100%);
    pointer-events: none;
    transition: all $effectDuration;

    &:target {
      opacity: 1;
      transform: translateY(0);
      pointer-events: all;
    }
  }
}