.news {
  &__content {
    padding-top: 90px;

    @include onTablet {
      padding-top: 120px;
    }

    @include onDesktop {
      padding-top: 110px;
    }
  }

  &__top {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 70px;
      @include pageGrid;
    }

    @include onDesktop {
      margin-bottom: 53px;
    }

    &-button {
      display: none;

      @include onTablet {
        display: flex;
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: 10 / -1;
      }
    }
  }

  &__title {
    @include onTablet {
      grid-column: span 4;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 50px;
    margin-bottom: 30px;

    @include onTablet {
      @include pageGrid;
      margin: 0;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  // &__card-img-wrapper {
  //   display: flex;

  //   @include onTablet {
  //     height: 420px;
  //   }

  //   @include onDesktop {
  //     height: 485px;
  //   }
  // }

  &__card-img {
    width: 100%;
    object-fit: cover;
    object-position: center;
    margin-bottom: 20px;
    min-height: 281px;
    transition: all $effectDuration;

    &:hover {
      transform: translateY(-8px);
      box-shadow: rgba(0, 0, 0, 0.3) 0 19px 38px, rgba(0, 0, 0, 0.22) 0 15px 12px;
    }

    @include onTablet {
      margin-bottom: 30px;
      min-height: 330px;
    }

    @include onDesktop {
      margin-bottom: 20px;
    }
  }

  &__card-data {
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }

  &__card-title {
    font-family: "Playfair Display", serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 125%;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 18px;
    }
  }

  &__card-description {
    color: $secondary-text-color;
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 40px;
      height: 72px;
    }

    @media (min-width: 917px) {
      height: max-content;
    }

    @include onDesktop {
      margin-bottom: 50px;
    }
  }

  &__card-button {
    @include onDesktop {
      max-width: 270px;
    }
  }
}