.menu {
  background-color: $secondary-bg-color;
  z-index: 2;
  overflow: auto;

  &__content {
    height: 100vh;
    position: relative;
    color: $white-color;
  }

  &__top {
    position: relative;
    z-index: 3;
    margin-bottom: 10px;

    @include onTablet {
      margin-bottom: 30px;
    }

    @include onDesktop {
      margin-bottom: 13px;
    }
  }

  &__bottom {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__info-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 40px;

    @include onTablet {
      grid-column: 1 / -1;
      @include pageGrid;
      margin-bottom: 50px;
    }

    @include onDesktop {
      margin-bottom: 70px;
    }
  }

  &__info {
    @include onTablet {
      grid-column: span 2;
    }

    &--grid-column {
      grid-column: span 3;
    }
  }

  &__info-name {
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }

  &__info-text {
    font-size: 16px;
    font-weight: 600;
    line-height: 150%;
    transition: color $effectDuration;

    &--hover {
      &:hover {
        color: $orange-color;
      }
    }
  }

  &__nav {
    margin-bottom: 40px;

    @include onTablet {
      grid-column: 1 / -1;
      margin-bottom: 50px;
    }

    @include onDesktop {
      grid-column: 1 / 7;
      margin-bottom: 60px;
    }
  }

  &__line {
    height: 1px;
    width: 100%;
    background-color: $white-color;
    margin-bottom: 40px;
    opacity: 0.3;

    @include onTablet {
      grid-column: 1 / -1;
      margin-bottom: 50px;
    }

    @include onDesktop {
      grid-column: 1 / 7;
      margin-bottom: 60px;
    }
  }

  &__button {
    max-width: 330px;

    @include onTablet {
      max-width: 100%;
      grid-column: 1 / 4;
      margin-bottom: 30px;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }

  &__img-wrapper {
    display: none;

    @include onTablet {
      display: flex;
      grid-column: 2 / 6;
    }

    @include onDesktop {
      position: absolute;
      right: -55px;
      bottom: 0;
      width: 624px;
    }
  }

  &__img {
    width: 100%;
  }
}