.form {

  &__field {
    padding: 0;
    width: 100%;
    margin-bottom: 30px;
    outline: none;
    border: none;
    border-bottom: 1px solid $main-text-color;
    background-color: transparent;
    color: $main-text-color;
    font-family: Raleway, sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 150%;
    caret-color: $orange-color;
    transition: all $effectDuration;

    @include onDesktop {
      margin-bottom: 40px;
    }

    &::placeholder {
      font-family: Raleway, sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;
    }

    &:hover,
    &:focus {
      border-bottom: 1px solid $orange-color;
    }

    &:hover::placeholder {
      transition: all $effectDuration;
      color: $orange-color;
    }

    &:focus::placeholder {
      color: transparent;
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active {
      transition: background-color 5000s ease-in-out 0s;
    }
  }

  &__button {
    outline: none;
    border: none;
  }
}