.visit {
  background-color: $secondary-bg-color;

  &__content {
    position: relative;
    color: $white-color;
    max-width: 1280px;
    margin: 0 auto;
    padding: 50px 0;

    @include onTablet {
      padding: 80px 0;
    }

    @include onDesktop {
      padding: 205px 0;
    }
  }

  &__image {
    display: none;

    @include onDesktop {
      display: block;
      position: absolute;
      width: 578px;
      height: 100%;
      bottom: 0;
      top: 0;
      left: 0;
    }
  }

  &__main-content {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__title {
    font-family: "Playfair Display", serif;
    font-size: 38px;
    font-weight: 700;
    line-height: 100%;
    margin-bottom: 20px;

    @include onTablet {
      font-size: 62px;
      grid-column: 1 /6;
    }

    @include onDesktop {
      font-size: 80px;
      grid-column: 6 / -1;
    }
  }

  &__description {
    margin-bottom: 40px;
    max-width: 450px;

    @include onTablet {
      grid-column: 1 / 5;
      max-width: 100%;
      margin-bottom: 60px;
    }

    @include onDesktop {
      grid-column: 6 / 11;
      margin-bottom: 50px;
    }
  }

  &__button {
    max-width: 330px;

    @include onTablet {
      grid-column: 1 / 4;
      max-width: 100%;
    }

    @include onDesktop {
      grid-column: 6 / 9;
    }
  }
}