.events {
  &__content {
    padding: 120px 0 90px;

    @include onTablet {
      padding: 160px 0 100px;
    }

    @include onDesktop {
      padding: 200px 0 110px;
    }
  }

  &__top {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 70px;
      @include pageGrid;
    }

    &-button {
      display: none;

      @include onTablet {
        display: flex;
        grid-column: 5 / -1;
      }

      @include onDesktop {
        grid-column: 10 / -1;
      }
    }
  }

  &__title {
    @include onTablet {
      grid-column: span 4;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 60px;
    margin-bottom: 30px;

    @include onTablet {
      gap: 70px;
    }

    @include onDesktop {
      gap: 60px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include onTablet {
      @include pageGrid;
    }
  }

  &__card-img {
    width: 100%;
    object-fit: cover;
    object-position: center;
    transition: all $effectDuration;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 4;
    }

    &:hover {
      transform: translateY(-8px);
      box-shadow: rgba(0, 0, 0, 0.3) 0 19px 38px, rgba(0, 0, 0, 0.22) 0 15px 12px;
    }
  }

  &__card-content {
    display: flex;
    flex-direction: column;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__card-data {
    color: $orange-color;
    margin-bottom: 10px;
  }

  &__card-title {
    font-family: "Playfair Display", serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 125%;
    margin-bottom: 20px;

    @include onTablet {
      font-size: 24px;
    }
  }

  &__card-description {
    color: $secondary-text-color;
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 50px;
      // height: 72px;
    }
  }

  &__card-button {
    @include onDesktop {
      max-width: 270px;
    }
  }
}