.nav {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include onTablet {
      gap: 30px;
    }

    @include onDesktop {
      gap: 60px;
    }

    &--footer {
      gap: 5px;

      @include onTablet {
        gap: 10px;
      }
    }
  }

  &__link {
    font-family: 'Playfair Display', serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 150%;
    transition: color $effectDuration;

    @include onTablet {
      font-size: 48px;
      line-height: 120%;
    }

    &--footer {
      font-family: Raleway, sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;
    }

    &:hover {
      color: $orange-color;
    }
  }
}