.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../images/icons/logo.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 123px;
  height: 40px;
  @include hover(transform, scale(1.1));

  @include onTablet {
    width: 124px;
    height: 37px;
  }
}