.footer {
  background-color: $secondary-bg-color;
  color: $white-color;
  padding: 34px 0 30px;

  @include onTablet {
    padding: 50px 0 30px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 50px;
    margin-bottom: 50px;

    @include onTablet {
      @include pageGrid;
    }

    @include onDesktop {
      margin-bottom: 60px;
    }
  }

  &__section {
    @include onTablet {
      grid-column: span 2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__section-title {
    margin-bottom: 10px;
    font-family: "Playfair Display", serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 150%;

    @include onTablet {
      font-size: 20px;
      margin-bottom: 30px;
    }
  }

  &__section-link {
    display: block;
    transition: color $effectDuration;

    &:hover {
      color: $orange-color;
    }
  }

  &__social {
    display: flex;
    gap: 20px;
    margin-top: 30px;

    @include onTablet {
      margin-top: 50px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__copyright {
    display: flex;
    gap: 22px;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;

    @include onDesktop {
      gap: 102px;
    }
  }

  &__scroll-up {
    transition: all 0.3s;
    stroke: $white-color;

    &:hover {
      transform: scale(1.1);
      stroke: $orange-color;
    }
  }
}