.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;

  @include onDesktop {
    padding: 27px 0;
  }

  &__icons {
    display: flex;
    gap: 30px;

    @include onTablet {
      gap: 40px;
    }

    &--menu {
      @include onTablet {
        flex-direction: row-reverse;
      }
    }
  }

  &__select {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    font-family: Raleway, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 150%;
    color: $white-color;

    &--header {
      display: none;

      @include onTablet {
        display: block;
      }
    }

    &:hover {
      color: $orange-color;
    }

    & option {
      background-color: $secondary-bg-color;
    }
  }
}