.subscription {
  &__content {
    padding: 120px 0 80px;

    @include onTablet {
      @include pageGrid;
      padding: 160px 0 128px;
    }

    @include onDesktop {
      padding: 200px 0;
    }
  }

  &__title {
    margin-bottom: 20px;

    @include onTablet {
      grid-column: 1 / -1;
    }

    @include onDesktop {
      grid-column: 1 / 7;
      margin-bottom: 50px;
    }
  }

  &__description {
    margin-bottom: 50px;
    max-width: 570px;

    @include onTablet {
      max-width: 100%;
      grid-column: 1 / 6;
    }

    @include onDesktop {
      grid-column: 7 / -1;
      margin-bottom: 0;
    }
  }

  &__form {
    max-width: 450px;

    @include onTablet {
      grid-column: 1 / 5;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 30px;
      max-width: 100%;
    }

    @include onDesktop {
      grid-column: 1 / 6;
      grid-template-columns: repeat(5, 1fr);
    }

    &-field {
      @include onTablet {
        grid-column: 1 / -1;
      }
    }

    &-button {
      max-width: 330px;

      @include onTablet {
        max-width: 100%;
        grid-column: 1 / 4;
      }
    }
  }
}